export default "() => {\n  const [startDate, setStartDate] = useState(new Date());\n  return (\n    <DatePicker\n      selected={startDate}\n      onChange={date => setStartDate(date)}\n    >\n      <div style={{ color: \"red\" }}>\n        Don't forget to check the weather!\n    </div>\n    </DatePicker>\n  );\n};\n"